.mainContainer {
    display: grid;
    width: 1280px;
    grid-template-columns: 1fr 480px 1fr;
    grid-template-rows: 1fr;
    gap: 60px;
    margin: 120px auto 0;
}

.container {
    margin: 0 0 0 auto;
    width: 400px;
    color: #8585AD;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 24px;
    max-width: 480px;
}

.text {
    height: 64px;
    line-height: 30px;
    padding: 20px 0 0 0;
}

.link {
    text-decoration: none;
    color: #8585AD
}

.active{
    color: white;
}

.containerInputs {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.containerSubmit {
    display: flex;
    justify-content: end;
    gap: 20px;
    margin-top: 24px;
}